<template>
    <div style="padding:20px">
        <div style="padding:10px 0">
            <div style="width:183px;height:50px;margin:0 auto">
                <img :src="logo" alt="" style="width:100%;height:100%">
            </div>
            
        </div>
        <div style="width:60%;margin:0 auto;text-align:center" class="clearfix ">
            <div v-for="(i,$index) in nab" class="fl nav-con"  @click="btn($index)">
                <router-link :to="i.ve" class="nav-con" :style="{'border-bottom':index==$index?'1px solid #000':'none'}">{{i.tt}}</router-link>
            </div>
        </div>
    </div>
</template>

<script>
import bus from "../component/bus.vue"
export default {
    data() {
        return {
            index:0,
            logo:"https://ccdn.goodq.top/caches/bb156c34ab1478d4698faf5d9b1fed10/aHR0cHM6Ly81Yjk3MzJmOTk0M2M3LnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTgvMDgvNDA5ZGI4ZjgzODFlOTMwNTJlMzUzNzNkMTI2ZTMwMzYucG5n.png",
            nab:[
                {
                    tt:"",
                    ve:"/"
                },
                {
                    tt:"",
                    ve:"/ppjs"
                },
                {
                    tt:"",
                    ve:"/cpzx"
                },
                {
                    tt:"",
                    ve:"/zxdt"
                },
                {
                    tt:"",
                    ve:"/yzjd"
                },
                {
                    tt:"",
                    ve:"/lxwm"
                }
            ]
        }
    },
    mounted() {
        bus.$on("lee",(a)=>{
            this.index=a
            // console.log(this.index)
        })
    },
    methods: {
        btn(i){
            this.index=i
            bus.$on("lee",(a)=>{
            this.index=a
            // console.log(this.index)
        })
        }
    },
}
</script>

<style lang="">
    .nav-con:hover{
        border-bottom:1px solid #000 !important
    }
    .nav-con{
        width:102px;margin:0 15px;height:66px;line-height:66px;
        display: inline-block;
    }
</style>


