import Vue from 'vue'
import Router from 'vue-router'
import index from './routes/index.vue'
import ppjs from "./routes/ppjs.vue"
import cpzx from "./routes/cpzx.vue"
import zxdt from "./routes/zxdt.vue"
import yzjd from "./routes/yzjd.vue"
import lxwm from "./routes/lxwm.vue"

Vue.use(Router)
var routes = [
    {
        path:"/",
        name:"index",
        component:index
    },
    {
        path:"/ppjs",
        name:"ppjs",
        component:ppjs,

    },
    {
        path:"/cpzx",
        name:"cpzx",
        component:cpzx,        
    },
    {
        path:"/zxdt",
        name:"zxdt",
        component:zxdt,
              
    },
    {
        path:"/yzjd",
                name:"yzjd",
                component:yzjd
    },
    {
        path:"/lxwm",
                name:"lxwm",
                component:lxwm
    }
]
export default new Router({
	routes
})


