<template>
    <div>
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="i in bannerimg">
                    <img :src="i" alt="">
                </div>               
            </div>
            <!-- Add Arrows -->
            <div class="sb-next">
                >
            </div>
            <div class="sb-prev">
                <
            </div>
        </div>
    </div>
</template>

<script>
import bus from "../component/bus.vue"
import Swiper from "../js/swiper.min.js"
export default {
    data() {
        return {
            selfindex:0,
            bannerimg:[
                "https://ccdn.goodq.top/caches/bb156c34ab1478d4698faf5d9b1fed10/aHR0cHM6Ly81Yjk3MzJmOTk0M2M3LnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTgvMDkvZWM1OTM5ZWUyNDMxNjg0MWRmMmJkOTM4MWJjN2IyYTEuanBn.jpg",
                "https://ccdn.goodq.top/caches/bb156c34ab1478d4698faf5d9b1fed10/aHR0cHM6Ly81Yjk3MzJmOTk0M2M3LnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTgvMDkvYzJhZjM3OWRjMDZkMDEwNDk0ZmJlNWZmYzcwNDc4ZDEuanBn.jpg"
            ]
        }
    },
    mounted() {
  bus.$emit("lee",this.selfindex)
    var swiper = new Swiper('.swiper-container', {
      navigation: {
        nextEl: '.sb-next',
        prevEl: '.sb-prev',
      },
      autoplay: {
        delay: 5000,//1
    },
    });

    },
}
</script>

<style lang="">
    .sb-next,.sb-prev{
        position: absolute;
        top:50%;
        width:70px;
        height:70px;
        color:#ddd;
        background-color:transparent;
        border-radius: 5px;
        text-align: center;
        line-height: 70px;
        font-size: 30px;
        z-index: 2;
        display: none;
    }
    .sb-prev{
        left: 0;
    }
    .sb-next{
        right: 0;
    }
    .swiper-container:hover .sb-next,.swiper-container:hover .sb-prev{
        display: block;
    }
    .sb-next:hover,.sb-prev:hover{
         background-color:rgba(0,0,0,.1);
    }
</style>


